@charset "UTF-8";
:root {
  --bb-bg-nav: #1E1E1E;
  --bb-pink: #F02666;
  --bb-green: #32806F;
  --bb-fluo: #64FF4F;
  --bb-blue: #3B86E9;
  --bb-salmon: #FF8A8A;
  --bb-purple: #8955FC;
  --bb-grey: #2E2E2E;
  --bb-red: #ED2D45;
  --bb-white: #FFFFFF;
  --bb-black: #000000;
}

@font-face {
  font-family: "Dogica";
  src: url(../../fonts/OTF/dogica.otf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VHSGothic";
  src: url(../../fonts/VHSGhotic/vhs-gothic.ttf);
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: "VHSGothic";
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: var(--bg-color);
  transition: background-color 0.4s;
}

nav,
main,
footer {
  width: 100%;
}

main {
  padding: 15rem 0 0;
  position: relative;
  flex: auto;
}

nav {
  margin: 2rem 0;
  position: fixed;
  height: 96px;
  z-index: 10;
}

footer {
  position: relative;
  bottom: 0;
  height: 62px;
  z-index: 5;
}

section {
  position: relative;
}

.position {
  position: absolute;
  top: 0;
  width: 100%;
}

.default-view {
  margin: 0 auto;
  padding: 0 2rem;
  max-width: 1000px;
  width: 100%;
  font-size: 1.2rem;
}

.main-page {
  position: relative;
  min-height: 50vh;
  transition: height 0.5s;
  overflow: hidden;
}

.stop-scrolling {
  height: 100%;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.nav-bar {
  position: fixed;
  width: 100%;
  z-index: 10;
}
.nav-bar span,
.nav-bar a {
  text-transform: uppercase;
}
.nav-bar__box {
  margin: 0 auto;
  padding: 0 2rem;
  display: grid;
  grid-template-columns: auto auto auto;
  height: 92px;
  max-width: 1800px;
  background: var(--bb-bg-nav);
}
.nav-bar__logo {
  align-self: flex-end;
  max-width: 200px;
}
.nav-bar__bb {
  font-size: 2.6rem;
  color: var(--bb-fluo);
}
.nav-bar__fest {
  display: inline-block;
  padding: 0.43rem 2rem;
  background-color: var(--bb-green);
  color: var(--bb-white);
}
.nav-bar__menu, .nav-bar__menu-mobile-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.nav-bar__menu a, .nav-bar__menu-mobile-list a {
  padding: 0 2rem;
  font-size: 1.6rem;
  text-transform: uppercase;
  cursor: pointer;
  transition: color 0.4s;
}
.nav-bar__menu a:nth-of-type(1), .nav-bar__menu-mobile-list a:nth-of-type(1) {
  color: var(--bb-pink);
}
.nav-bar__menu a:nth-of-type(2), .nav-bar__menu-mobile-list a:nth-of-type(2) {
  color: var(--bb-blue);
}
.nav-bar__menu a:nth-of-type(3), .nav-bar__menu-mobile-list a:nth-of-type(3) {
  color: var(--bb-green);
}
.nav-bar__menu a:nth-of-type(4), .nav-bar__menu-mobile-list a:nth-of-type(4) {
  color: var(--bb-salmon);
}
.nav-bar__menu a:nth-of-type(5), .nav-bar__menu-mobile-list a:nth-of-type(5) {
  color: var(--bb-fluo);
}
.nav-bar__menu a:nth-of-type(6), .nav-bar__menu-mobile-list a:nth-of-type(6) {
  color: var(--bb-purple);
}
.nav-bar__menu a:hover, .nav-bar__menu-mobile-list a:hover {
  color: var(--bb-white);
}
.nav-bar__menu-mobile {
  display: none;
}
.nav-bar__more {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}
.nav-bar__date, .nav-bar__place {
  padding: 0 0.8rem;
  color: var(--bb-white);
  z-index: 11;
}
.nav-bar__date {
  font-size: 1.6rem;
}
.nav-bar__place {
  font-size: 1.2rem;
  line-height: 2.2rem;
}
.nav-bar__flower {
  position: absolute;
  top: -2rem;
  right: 0;
  z-index: 10;
}

.title {
  padding: 2rem 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-content: center;
}
.title__box {
  position: relative;
  padding: 1.4rem 0;
  display: grid;
  grid-template-columns: 1fr 195px 170px 1fr;
  text-transform: uppercase;
  background-color: #000000;
  max-width: 510px;
  width: 100%;
}
.title__box .title-menu-checked {
  margin: 0;
  position: absolute;
  top: -5px;
  left: -189px;
  width: 110px;
  height: 110px;
  background: var(--title-bg-color);
  color: var(--bb-white);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--bb-white);
  text-transform: uppercase;
  border: 0.05rem solid black;
  opacity: 0;
  pointer-events: none;
  transition: all 0.4s;
}
.title__box .title-menu-checked h2 {
  font-size: 1.2rem;
}
.title__box .close-menu {
  position: absolute;
  right: -15%;
  top: 0%;
  transform: translateX(-15%);
}
.title__box .close-menu__btn {
  padding: 0.5rem;
  font-size: 3rem;
  color: var(--bb-red);
  background-color: var(--bb-black);
  border: none;
  opacity: 0;
  transition: opacity 0.4s, background-color 0.4s, color 0.4s;
  cursor: pointer;
}
.title__box .close-menu__btn:hover {
  background-color: var(--bb-white);
  color: var(--bb-black);
}
.title__box-left {
  position: relative;
  grid-column: 2/3;
}
.title__box-left h1 {
  font-size: 3.5rem;
  letter-spacing: -1rem;
  color: var(--bb-red);
}
.title__box-left h1,
.title__box-left span {
  font-family: "Dogica";
}
.title__box-left span {
  position: absolute;
  bottom: -2.3rem;
  left: 0;
  padding: 0.5rem 2rem;
  background-color: var(--bb-green);
  color: var(--bb-white);
  font-size: 1rem;
  text-transform: uppercase;
}
.title__box-right {
  grid-column: 3/4;
  color: var(--bb-white);
}
.title__box-right span {
  display: block;
}
.title__box-right span:nth-of-type(1) {
  font-size: 1.6rem;
  margin-bottom: 1.5rem;
}
.title__box-right span:nth-of-type(2) {
  font-size: 1.2rem;
}

.body-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.body-menu__box {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.body-menu__cards {
  margin: 5rem 0;
  display: grid;
  grid-template-columns: repeat(4, minmax(200px, auto));
  grid-template-rows: 200px;
}
.body-menu__card {
  position: relative;
  margin: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--bb-white);
  text-transform: uppercase;
  cursor: pointer;
  border: 0.05rem solid black;
  transition: background-color 0.4s, color 0.4s;
}
.body-menu__card img {
  display: none;
}
.body-menu__card h2 {
  max-width: 120px;
  font-size: 1.4rem;
  line-height: 4.2rem;
}
.body-menu__card:nth-of-type(1) {
  background-color: var(--bb-pink);
}
.body-menu__card:nth-of-type(2) {
  background-color: var(--bb-blue);
}
.body-menu__card:nth-of-type(3) {
  background-color: var(--bb-salmon);
}
.body-menu__card:nth-of-type(4) {
  background-color: var(--bb-fluo);
}
.body-menu__card:hover {
  background-color: var(--bb-white);
  color: var(--bb-black);
}
.body-menu__co-organizer h2 {
  margin: 4rem 0 0;
  font-size: 2rem;
}

.info {
  opacity: 0;
  padding: 2rem 0;
  pointer-events: none;
  transition: all 0.4s;
}
.info__box {
  padding: 2rem 5rem;
  background-color: #cdacc1;
}
.info h3,
.info h4 {
  padding: 2rem 0 0.7rem;
}
.info span {
  padding: 0.2rem 0;
  display: block;
}
.info ul {
  margin-left: 3rem;
}
.info.active {
  opacity: 1;
  pointer-events: all;
}

.lineUp {
  padding: 2rem 0;
  opacity: 0;
  pointer-events: none;
}
.lineUp__box {
  padding: 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  background-color: #3f4c5e;
}
.lineUp__card {
  position: relative;
  margin: 1rem;
  overflow: hidden;
}
.lineUp__card:hover > .lineUp__card-back {
  clip-path: circle(120% at 50% 100%);
  pointer-events: auto;
}
.lineUp__card-front img {
  width: 100%;
  height: auto;
}
.lineUp__card-back {
  position: absolute;
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  transform: translate(-50%, -50%);
  width: 101%;
  height: 100%;
  background-color: var(--bb-purple);
  clip-path: circle(0% at 50% 100%);
  transition: clip-path 0.8s;
  will-change: clip-path;
  pointer-events: none;
}
.lineUp__card-back-title h3 {
  color: var(--bb-fluo);
  font-size: 2.6rem;
  text-transform: uppercase;
}
.lineUp__card-back-title span {
  display: inline-block;
  padding: 0.43rem 2rem;
  background-color: var(--bb-green);
  color: var(--bb-white);
}
.lineUp__card-back-social a {
  padding: 0 0.6rem;
}
.lineUp__card-back-social a svg {
  width: 5rem;
  height: 5rem;
  stroke: var(--bb-black);
  transition: stroke 0.4s, fill 0.4s;
}
.lineUp__card-back-social a:nth-of-type(2) svg {
  fill: var(--bb-black);
}
.lineUp__card-back-social a:hover:nth-of-type(2) svg {
  fill: var(--bb-salmon);
}
.lineUp__card-back-social a:hover svg {
  stroke: var(--bb-salmon);
}
.lineUp__card-back-date {
  display: flex;
}
.lineUp__card-back-date span {
  margin: 0 1rem;
}
.lineUp.active {
  opacity: 1;
  pointer-events: all;
}

.tickets {
  opacity: 0;
  pointer-events: none;
  color: var(--bb-white);
}
.tickets__box {
  margin: 0 auto;
  max-width: 600px;
  background: rgba(0, 0, 0, 0.6);
}
.tickets__title {
  padding: 2rem;
  text-align: center;
}
.tickets__title a {
  color: var(--bb-red);
}
.tickets__title span {
  color: var(--bb-fluo);
}
.tickets__form {
  position: relative;
  margin: 0 auto;
  padding: 3rem 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.tickets__form h4 {
  margin-bottom: 2rem;
  padding: 1rem 3rem;
  display: block;
  background-color: var(--bb-red);
  color: var(--bb-white);
}
.tickets__form h5 {
  margin: 0 auto;
  padding: 3rem 1rem;
  color: var(--bb-white);
  text-align: center;
  font-size: 1.4rem;
}
.tickets__form::before {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  font-size: 2.2rem;
  z-index: 5;
  content: "START REJESTRACJI WKRÓTCE!";
}
.tickets__form-row-box {
  max-width: 450px;
  width: 100%;
}
.tickets__form-row {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: minmax(auto, 35px);
}
.tickets__form-row label {
  margin: auto 0;
  padding: 0 1rem;
  font-size: 1.8rem;
  text-transform: uppercase;
}
.tickets__form-row:nth-of-type(1) label {
  color: var(--bb-salmon);
}
.tickets__form-row:nth-of-type(2) label {
  color: var(--bb-blue);
}
.tickets__form-row:nth-of-type(3) label {
  color: var(--bb-red);
}
.tickets__form-row:nth-of-type(4) label {
  color: var(--bb-fluo);
}
.tickets__form-row:nth-of-type(5) {
  position: relative;
  grid-template-columns: 1fr auto 1fr;
}
.tickets__form-row:nth-of-type(5) label {
  justify-self: flex-end;
  display: inline-block;
  font-size: 0.8rem;
  text-align: start;
  max-width: 220px;
}
.tickets__form-row:nth-of-type(5) input {
  position: relative;
  opacity: 0;
  cursor: pointer;
}
.tickets__form-row:nth-of-type(5) span {
  display: inline-block;
  border: 3px solid #f72664;
  background-color: white;
  box-sizing: border-box;
  width: 20px;
  height: 20px;
}
.tickets__form-row:nth-of-type(5) span:has(> input:checked) {
  background-color: #f72664;
}
.tickets button {
  display: block;
  margin: 0 0 0 auto;
  padding: 0.8rem 2rem;
  width: auto;
  height: 100%;
  background: var(--bb-fluo);
  border: none;
  color: var(--bb-red);
  font-size: 1.6rem;
  font-family: "VHSGothic";
  text-transform: uppercase;
  cursor: pointer;
}
.tickets.active {
  opacity: 1;
  pointer-events: all;
}

.contact {
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  opacity: 0;
  pointer-events: none;
}
.contact__box {
  margin: 0 auto;
  padding: 4rem 0;
  max-width: 890px;
  background-color: rgba(0, 0, 0, 0.6);
  color: var(--bb-white);
  border-radius: 0.8rem;
}
.contact__title-text {
  margin: 0 auto;
  max-width: 500px;
}
.contact__title-text p {
  text-align: center;
  font-size: 1.4rem;
  line-height: 2.6rem;
}
.contact__adress-list {
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-content: center;
}
.contact__adress-list span {
  padding: 2rem 0;
  font-size: 2rem;
  color: var(--bb-white);
  text-transform: uppercase;
}
.contact__adress-list span:nth-of-type(1) a {
  color: var(--bb-purple);
}
.contact__adress-list span:nth-of-type(2) a {
  color: var(--bb-pink);
}
.contact__adress-list span:nth-of-type(3) a {
  color: var(--bb-salmon);
}
.contact__adress-list span:nth-of-type(4) a {
  color: var(--bb-fluo);
}
.contact__adress-list span a {
  text-transform: lowercase;
}
.contact.active {
  opacity: 1;
  pointer-events: all;
}

.foot {
  width: 100%;
  margin: 0.5rem auto 0;
  overflow-x: hidden;
}
.foot__box {
  padding: 2rem 3rem;
  display: grid;
  grid-template-columns: 290px auto 290px;
  width: 100%;
}
.foot__box h3,
.foot__box a {
  font-size: 1rem;
  color: var(--bb-black);
}
.foot__tech, .foot__copy-right, .foot__production {
  display: flex;
  align-items: center;
}
.foot__tech h3, .foot__copy-right h3, .foot__production h3 {
  padding: 0.5rem 1rem;
}
.foot__tech:nth-of-type(1), .foot__copy-right:nth-of-type(1), .foot__production:nth-of-type(1) {
  justify-content: flex-start;
}
.foot__tech:nth-of-type(2), .foot__copy-right:nth-of-type(2), .foot__production:nth-of-type(2) {
  justify-content: center;
}
.foot__tech:nth-of-type(3), .foot__copy-right:nth-of-type(3), .foot__production:nth-of-type(3) {
  justify-content: flex-end;
}

.bbFlowerBlue,
.bbHand,
.bbFace {
  position: absolute;
  height: auto;
  z-index: -1;
}

.bbFlowerBlue {
  right: 0;
  bottom: 0;
}

.bbFace {
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.bbHand {
  top: 0;
  left: -100px;
  max-height: 100vh;
}

.show-menu .nav-bar__menu-mobile-box {
  transform: translateX(0%);
}

.active .body-menu {
  display: none;
}
.active .title-menu-checked {
  opacity: 1;
  pointer-events: all;
}
.active .title__box .close-menu__btn {
  opacity: 1;
}

@media screen and (max-width: 1200px) {
  .nav-bar__more {
    padding-left: 2rem;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  .body-menu::after {
    background: none;
  }
  .body-menu__cards {
    grid-template-columns: repeat(4, minmax(180px, auto));
    grid-template-rows: 180px;
  }
}
@media screen and (max-width: 1025px) and (orientation: landscape) {
  .title__box {
    margin: 2rem 0 0;
  }
  .body-menu > img:nth-of-type(3) {
    display: none;
  }
  .body-menu__box {
    margin: 10rem 0 0;
  }
  .body-menu__cards {
    margin: 2rem 0 0;
  }
  .body-menu__cards {
    grid-template-columns: repeat(4, minmax(160px, auto));
    grid-template-rows: 160px;
  }
  .body-menu__card h2 {
    max-width: 100px;
    font-size: 1.2rem;
  }
}
@media screen and (max-width: 1100px) {
  .nav-bar__menu a {
    padding: 0 1.8rem;
    font-size: 1.4rem;
  }
}
@media screen and (max-width: 992px) {
  nav {
    margin: 0 0 2rem;
  }
  .title__box .title-menu-checked {
    left: -120px;
    top: 0;
    width: 98px;
    height: 98px;
  }
  .body-menu > img {
    display: none;
  }
  .nav-bar__box {
    grid-template-columns: 150px auto 150px;
  }
  .nav-bar__menu {
    display: none;
  }
  .nav-bar__menu-mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .nav-bar__menu-mobile button {
    padding: 0.6rem 2.4rem;
    text-transform: uppercase;
    font-family: "VHSGothic";
    font-size: 2rem;
    letter-spacing: 0.4rem;
    color: var(--bb-red);
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: background-color 0.4s, color 0.4s;
  }
  .nav-bar__menu-mobile button:hover {
    background-color: var(--bb-white);
    color: var(--bb-grey);
  }
  .nav-bar__menu-mobile-box {
    position: absolute;
    top: 91px;
    left: 0;
    width: 100%;
    height: calc(100vh - 91px);
    background: rgba(0, 0, 0, 0.6);
    transform: translateX(-100%);
    transition: transform 0.4s ease-in-out;
  }
  .nav-bar__menu-mobile-list {
    padding: 5rem 0 15rem;
    flex-direction: column;
    justify-content: space-around;
    height: 100%;
    width: 50%;
    background: var(--bb-bg-nav);
  }
  .nav-bar__menu-mobile-list a:hover {
    color: var(--bb-white);
  }
  .nav-bar__flower img {
    max-width: 100px;
  }
  .lineUp__card-back {
    clip-path: circle(10% at 50% 100%);
  }
  .foot__box {
    grid-template-columns: 240px auto 175px;
  }
  .foot__box h3,
  .foot__box a {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 768px) {
  body {
    min-height: calc(100vh - 92px);
  }
  main {
    padding: 12rem 0 0;
  }
  .title-menu-checked {
    display: none !important;
  }
  .default-view {
    padding: 0 1rem;
  }
  nav {
    height: 92px;
  }
  .nav-bar {
    margin: 0 0 2rem;
  }
  .nav-bar__box {
    padding: 0 1rem;
    grid-template-columns: 90px auto 90px;
  }
  .nav-bar__logo {
    align-self: center;
  }
  .nav-bar__logo :nth-of-type(2) {
    font-size: 0.6rem;
  }
  .nav-bar__bb {
    font-size: 1.6rem;
  }
  .nav-bar__more {
    padding-left: 0;
  }
  .nav-bar__date {
    margin-bottom: 0.6rem;
    font-size: 1rem;
  }
  .nav-bar__place {
    font-size: 0.8rem;
    line-height: 1.2rem;
  }
  .nav-bar__menu-mobile button {
    padding: 0.4rem 1.8rem;
    font-size: 1.6rem;
  }
  .title__box {
    margin: 0;
    grid-template-columns: auto auto;
    justify-content: center;
    gap: 2rem;
    max-width: 320px;
  }
  .title__box-left {
    grid-column: 1/2;
  }
  .title__box-left h1 {
    width: min-content;
    font-size: 2.6rem;
  }
  .title__box-right {
    grid-column: 2/3;
  }
  .title__box-right span:nth-of-type(1) {
    font-size: 1.2rem;
  }
  .title__box-right span:nth-of-type(2) {
    font-size: 1rem;
  }
  .body-menu__box {
    position: relative;
    padding: 0 1rem;
  }
  .body-menu__cards {
    margin: 2rem 0 0;
    grid-template-rows: repeat(4, 150px);
    grid-template-columns: 1fr 1fr;
  }
  .body-menu__card {
    margin: 0;
    width: 150px;
    border: 0.01rem solid black;
  }
  .body-menu__card img {
    display: block;
    position: absolute;
    max-width: 200px;
    z-index: -1;
    overflow: hidden;
    transform: translate(0);
  }
  .body-menu__card:nth-of-type(1) {
    grid-row: 1/2;
  }
  .body-menu__card:nth-of-type(1) img {
    top: 101%;
  }
  .body-menu__card:nth-of-type(2) {
    grid-area: 2/2/3/3;
  }
  .body-menu__card:nth-of-type(2) img {
    top: -111.5%;
  }
  .body-menu__card:nth-of-type(3) {
    grid-row: 3/4;
  }
  .body-menu__card:nth-of-type(3) img {
    top: 100%;
  }
  .body-menu__card:nth-of-type(4) {
    grid-area: 4/2/5/3;
  }
  .body-menu__card:nth-of-type(4) img {
    top: -100%;
  }
  .body-menu__co-organizer h2 {
    text-align: center;
  }
  .body-menu::after {
    display: none;
  }
  .info__box,
  .lineUp__box,
  .contact__box {
    padding: 2rem 1rem;
  }
  .contact__title-text p {
    font-size: 1.2rem;
  }
  .contact__adress-list span {
    padding: 1rem 0;
    font-size: 1.4rem;
    text-align: center;
  }
  .contact__adress-list span a {
    padding: 0.5rem 0;
    display: block;
  }
  .foot {
    position: relative;
  }
  .foot > img {
    max-width: 150px;
  }
  .foot__box {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }
  .foot__tech:nth-of-type(odd), .foot__copy-right:nth-of-type(odd), .foot__production:nth-of-type(odd) {
    justify-content: center;
  }
  .foot__tech {
    grid-row: 3/4;
  }
  .foot__copy-right {
    grid-row: 1/2;
  }
  .foot__production {
    grid-row: 2/3;
  }
  .bbFlowerBlue {
    bottom: -40px;
    max-width: 150px;
  }
  .bbFace,
  .bbHand {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  .nav-bar__menu-mobile-list {
    width: 100%;
  }
}
@media screen and (max-width: 480px) {
  .title__box .close-menu {
    top: -55%;
    right: 0;
  }
  .title__box .close-menu__btn {
    font-size: 2.6rem;
  }
}